.mountains {
  height: 100%;
  width: 100%;
  z-index: 3;
  clip-path: polygon(0% 80%, 25% 50%, 40% 70%, 60% 35%, 65% 45%, 80% 20%, 100% 80%, 100% 100%, 0% 100%);
  background-color: #1d7371;
  transition: all .5s ease-in-out;
  position: absolute;
}

.cloud {
  height: 4px;
  width: 9px;
  filter: opacity(.5);
  background-color: #fff;
  border-radius: 9999px;
  position: absolute;
  top: 20%;
  left: 10%;
}

.sea {
  height: 50px;
  width: 150%;
  background: linear-gradient(#3f75cc, #06063b, #06063b, #06063b, #06063b);
  border-radius: 50%;
  transition: all .5s ease-in-out;
  position: absolute;
  bottom: -250%;
  right: 50%;
  transform: translateX(50%);
}

.sea:before {
  content: "";
  height: 12.5px;
  width: 7.5px;
  filter: blur(2.5px);
  background-color: rgba(254, 254, 254, .34);
  position: absolute;
  top: 2%;
  left: 43%;
}

.star {
  height: 1px;
  width: 1px;
  filter: blur(.4px);
  background-color: #fff;
  border-radius: 50%;
  transition: all .5s ease-in-out;
  position: absolute;
  top: -50%;
  right: 15%;
}

.star:before {
  content: "";
  height: 50%;
  width: 50%;
  background-color: #fff;
  border-radius: 50%;
  transition: all .5s ease-in-out;
  position: absolute;
  top: -50%;
  right: 14px;
}

.star:after {
  content: "";
  height: 50%;
  width: 50%;
  background-color: #fff;
  border-radius: 50%;
  transition: all .5s ease-in-out;
  position: absolute;
  top: 5px;
  right: -18px;
}

.cloud:before {
  content: "";
  height: 5px;
  width: 5px;
  background-color: #fff;
  border-radius: 50%;
  position: absolute;
  top: -50%;
  right: 50%;
  transform: translateX(50%);
}

#checkbox:checked + .toggle {
  background: linear-gradient(#036daf, #003d63);
}

.toggle:before {
  content: "";
  height: 20px;
  width: 20px;
  background-color: #ffd34d;
  border-radius: 50%;
  transition: all .5s ease-in-out;
  position: absolute;
  top: 10%;
  right: 30%;
  box-shadow: 0 0 7.5px rgba(254, 249, 95, .765);
}

.toggle:after {
  content: "";
  height: 17.5px;
  width: 17.5px;
  background-color: #fff;
  border-radius: 50%;
  transition: all .5s ease-in-out;
  position: absolute;
  top: 10%;
  left: -60%;
  box-shadow: 0 0 5px rgba(255, 255, 255, .533);
}

#checkbox:checked + .toggle:before {
  transform: translateX(220%);
}

#checkbox:checked + .toggle:after {
  transform: translateX(370%);
}

#checkbox:checked + .toggle .mountains {
  background-color: #05021a;
  transform: translateY(100%);
}

#checkbox:checked + .toggle .sea {
  background-color: #05021a;
  bottom: -125%;
}

#checkbox:checked + .toggle .star {
  top: 20%;
  transform: rotate(10deg);
}

#checkbox:checked + .toggle, #burger-checkbox:checked + .toggle {
  background: linear-gradient(#036daf, #003d63);
}

#burger-checkbox:checked + .toggle:before {
  transform: translateX(220%);
}

#burger-checkbox:checked + .toggle:after {
  height: 14px;
  width: 14px;
  transform: translateX(360%);
}

#checkbox:checked + .toggle .mountains, #burger-checkbox:checked + .toggle .mountains {
  background-color: #05021a;
  transform: translateY(100%);
}

#checkbox:checked + .toggle .sea {
  background-color: #05021a;
  bottom: -125%;
}

#burger-checkbox:checked + .toggle .sea {
  background-color: #05021a;
  bottom: -167%;
}

#checkbox:checked + .toggle .star, #burger-checkbox:checked + .toggle .star {
  top: 20%;
  transform: rotate(10deg);
}

@media only screen and (min-width: 767px) {
  #burger-checkbox:checked + .toggle:after {
    height: 16px;
    width: 16px;
    transform: translateX(397%);
  }

  #burger-checkbox:checked + .toggle .sea {
    background-color: #05021a;
    bottom: -118%;
  }
}

